.App {
  text-align: center;
  background-color: #f4c53699;

}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.create{
  padding: 20px;
}

nav {
  position: sticky;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1.5rem 2rem;
  background-color: #ff6200ce;
}

.logo {
  padding: 1.5rem 2rem;
  background-color: rgba(0, 0, 255, 0.828);
  border-style: groove;
  font-weight: bold;
  color: white;
  font-size: 40px;
}

.link{
  font-size: 30px;
  padding: 1.5rem 2rem;
  font-style: normal;
  font-weight: bold
}

a:link, a:visited {
  background-color: #f44336;
  border-radius: 8px;
  border-width: 3em;
  color: white;
  padding: 14px 25px;
  margin-right: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

a:hover, a:active {
  background-color: red;
}

.acc{
  font-size: 17px;
  font-style: normal;
}

p {
  font-size: 10px;
}

label {
  padding: 10px,50px;
  margin: 10px,50px;
  font-size: 25px;

}



.mainInput {


  padding: 20px;
}


input {
  font-size: 20px;
  padding:5px;
  border:0;
  box-shadow:0 0 2px 1px black;
  
}

input[type=text] {
  padding:10px;
  border:0;
  box-shadow:0 0 4px, 3px, black;
  border-radius: 8px;
}

.btn {
  background-color: #f44336;
  font-size: 20px;
  font-weight: bold;
  padding: 16px, 5px;
  width: 50%;

  border-color: #f44336;
  border-radius: 16px;


}

.summit {
  background-color: #f44336;

  padding: 32px 10px;
  font-size: 20px;
  font-weight: bold;
  width: 80%;
}

.btn:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);

}


.poll {
  background-color: rgba(172, 255, 47, 0.551);
  margin: 15px;
  padding: 5px;
  border-radius: 16px;
  border-style: solid;
  border-width: 4px;
  border-color: black;

}

.countdown {
  font-size: 30px;
  font-weight: bold;
}

table {

  width: 100%;
  font-size: 22px;
}

th, td {
  padding: 10px;
  text-align: center;
}

tr:hover {
  background-color: greenyellow;
}